<template>
  <section class="container mt-5">
    <section class="mb-5 text-center py-6">
     <h1>親愛的顧客您好，感謝您的訂購。</h1>
    </section>    
    <section class="step1 border mb-5 mx-auto">
      <div class="py-4" v-for="(item,index) in cartData" :key="index">
        <div class="flex-center mx-auto w-75 position-relative">
          <img :src="item.imgUrl" height="70px">
          <div>
            <a>
              <h5>{{item.fullId}} {{item.title}}-{{item.gender}}</h5>
            </a>
            <h5>規格: {{item.colorObject.name}} - {{item.sizeObject.name}}</h5>
          </div>
          <div class="flex-center ml-auto">
            {{item.qty}}件
          </div>
          <div class="flex-center ml-4">$ {{item.qty*item.price}}</div>
        </div>
      </div>
    </section>
    <section class="price-section d-flex mb-5">
      <table class="ml-auto w-50 text-right p-2">
        <td>
          <tr>共{{totalNum}}件</tr>
          <tr></tr>
          <tr></tr>
          <tr></tr>
        </td>
        <td>
          <tr>商品金額</tr>
          <tr>運費小計</tr>
          <tr>帳戶扣抵</tr>
          <tr>總計</tr>
        </td>
        <td>
          <tr>${{totalPrice}}</tr>
          <tr>$0</tr>
          <tr>$0</tr>
          <tr class="text-price h5">${{totalPrice}}</tr>
        </td>
      </table>
    </section>
    <section class="border w-100">
      <div class="border-bottom">
        <div class="w-75 mx-auto py-3 h4">取貨人資訊</div>
      </div>
      <div class="w-75 mx-auto border-bottom py-3 d-flex">
        <div class="w-25 h4">付款方式</div>
        <div class="w-75 h4">{{payType}}</div>
      </div>
      <div class="w-75 mx-auto border-bottom py-3 d-flex">
        <div class="w-25 h4">訂購姓名</div>
        <div class="w-75 h4">{{receiverData.name}}</div>
      </div>
      <div class="w-75 mx-auto border-bottom py-3 d-flex">
        <div class="w-25 h4">收貨地址</div>
        <div class="w-75 h4">{{receiverData.address}}</div>
      </div>
      <div class="w-75 mx-auto py-3 d-flex">
        <div class="w-25 h4">發票類型</div>
        <div class="w-75 h4">{{receiverData.invoice}}</div>
      </div>
    </section>
  </section>
</template>

<script>
export default {
  name:'check3',
  data() {
    return{
      cartData:[],
      receiverData:{},
      payType: ''
    }
  },
  computed: {
    step() {
      return this.$store.state.checkStep
    },
    totalNum() {
      let tempNum = 0
      this.cartData.forEach(element => {
        tempNum += element.qty
      });
      return tempNum
    },
    totalPrice() {
      let tempPrice = 0
      this.cartData.forEach(element => {
        tempPrice += element.qty*element.price
      })
      return tempPrice
    },
  },
  created() {
    this.cartData = this.$store.state.cart
    this.receiverData = this.$store.state.receiverData
    this.payType = this.$store.state.payType
    this.$store.commit("CHECKSTEP", 3)
  }
}
</script>
<style lang="scss" scoped>
.price-section {
  background-color: #fbfbfb;
}
h1,h2,h3,h4,h5,h6,.h1,h2,h3,h4,h5,h6{
  margin-bottom: 0;
}
</style>